/*---------------------------------------------------------------------------------------------
*
*  Field Groups
*
*---------------------------------------------------------------------------------------------*/
.acf-admin-field-groups {

	// Hide tablenav top.
	.tablenav.top {
		display: none;
	}

	// Fix margin due to hidden tablenav.
	.subsubsub {
		margin-bottom: 3px;
	}

	// table.
	.wp-list-table {
		margin: {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
		}
		border-radius: $radius-lg;
		border: none;
		overflow: hidden;
		box-shadow: $elevation-01;

		strong {
			color: $gray-400;
			margin: 0;
		}

		a.row-title {
			font-size: 13.5px !important;
			font-weight: 500;
		}

		th,
		td {
			color: $gray-700;

			&.sortable a {
				padding: 0;
			}

			&.check-column {
				padding: {
					right: 16px;
					left: 16px;
				};

				@media screen and (max-width: $md) {
					vertical-align: top;
					padding: {
						right: 2px;
						left: 10px;
					};
				}

			}

			input {
				margin: 0;
				padding: 0;
			}

		}

		// Table headers
		thead th, thead td,
		tfoot th, tfoot td {
			height: 48px;
			padding: {
				right: 24px;
				left: 24px;
			};
			box-sizing: border-box;
			background-color: $gray-50;
			border-color: $gray-200;
			@extend .p3;
			font-weight: 500;

			@media screen and (max-width: $md) {
				padding: {
					right: 16px;
					left: 8px;
				};
			}

			&.check-column {
				@media screen and (max-width: $md) {
					vertical-align: middle;
				}
			}

		}

		// Table body
		tbody th,
		tbody td {
			box-sizing: border-box;
			height: 56px;
			padding: {
				top: 8px;
				right: 24px;
				bottom: 8px;
				left: 24px;
			};
			vertical-align: middle;
			background-color: #fff;
			border-bottom: {
				width: 1px;
				color: $gray-200;
				style: solid;
			};
			@extend .p4;

			@media screen and (max-width: $md) {
				padding: {
					right: 16px;
					left: 8px;
				};
			}

		}

		.column-acf-key {
			white-space: nowrap;
		}

		// SVG icons
		.column-acf-key .acf-icon-key-solid {
			display: inline-block;
			position: relative;
			bottom: -2px;
			width: 15px;
			height: 15px;
			margin: {
				right: 4px;
			};
			color: $gray-400;
		}

		// Post location icon
		.acf-location .dashicons {
			position: relative;
			bottom: -2px;
			width: 16px;
			height: 16px;
			margin: {
				right: 6px;
			};
			font-size: 16px;
			color: $gray-400;
		}

		.post-state {
			@extend .p3;
			color: $gray-500;
		}

		// Add subtle hover background to define row.
		tr:hover,
		tr:focus-within {
			background: #f7f7f7;

			.row-actions {
				margin: {
					bottom: 0;
				};
			};

		}

		// Use less specific identifier to inherit mobile styling.
		@media screen and ( min-width: 782px ) {
			.column-acf-count { width: 10%; }
		}

		.row-actions {
			span.file {
				display: block;
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
		}
	}

	// Actions
	.row-actions {
		padding: {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
		}
		@extend .p5;
		color: $gray-300;

		.trash a {
			color: $acf_error;
		}

	}


	// Remove padding from checkbox column
	.widefat thead td.check-column,
	.widefat tfoot td.check-column {
		padding: {
			top: 0;
		};
	}

}

/*--------------------------------------------------------------------------------------------
*
*	Row actions
*
*--------------------------------------------------------------------------------------------*/
.acf-admin-field-groups {

	.row-actions {
		margin: {
			bottom: -20px;
		};
		@extend .p6;

		a:hover {
			color: darken($color-primary-hover, 10%);
		}

		.trash a {
			color: #a00;
			&:hover { color: #f00; }
		}

		&.visible {
			margin: {
				bottom: 0;
			};
			opacity: 1;
		}

	}

}


/*--------------------------------------------------------------------------------------------
*
*	Row hover
*
*--------------------------------------------------------------------------------------------*/
.acf-admin-field-groups {

	#the-list tr:hover td,
	#the-list tr:hover th {
		background-color: lighten($blue-50, 3%);
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Table Nav
*
*---------------------------------------------------------------------------------------------*/
.acf-admin-field-groups {

	.tablenav {
		margin: {
			top: 24px;
			right: 0;
			bottom: 0;
			left: 0;
		};
		padding: {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
		};
		color: $gray-500;
	}

}

/*--------------------------------------------------------------------------------------------
*
*	Search box
*
*--------------------------------------------------------------------------------------------*/
.acf-admin-field-groups #posts-filter p.search-box {
	margin: {
		top: 5px;
		right: 0;
		bottom: 24px;
		left: 0;
	};

	#post-search-input {
		min-width: 280px;
		margin: {
			top: 0;
			right: 8px;
			bottom: 0;
			left: 0;
		};
	}

	@media screen and (max-width: 768px) {
		display: flex;
		box-sizing: border-box;
		padding-right: 24px;
		margin-right: 16px;

		#post-search-input {
			min-width: auto;
		}

	}

}

/*--------------------------------------------------------------------------------------------
*
*	Status tabs
*
*--------------------------------------------------------------------------------------------*/
.acf-admin-field-groups .subsubsub {
	display: flex;
	align-items: flex-end;
	height: 40px;
	margin: {
		bottom: 16px;
	};

	li {
		margin: {
			top: 0;
			right: 4px;
		};
		color: $gray-400;
		@extend .p4;

		.count {
			color: $gray-500;
		}

	}

}

/*--------------------------------------------------------------------------------------------
*
*	Pagination
*
*--------------------------------------------------------------------------------------------*/
.acf-admin-field-groups {

	.tablenav-pages {
		display: flex;
		align-items: center;

		.displaying-num {
			margin: {
				top: 0;
				right: 16px;
				bottom: 0;
				left: 0;
			};
		}

		.pagination-links {
			display: flex;
			align-items: center;

			#table-paging {
				margin: {
					top: 0;
					right: 4px;
					bottom: 0;
					left: 8px;
				};

				.total-pages {
					margin: {
						right: 0;
					};
				}

			}

		}

		// Hide pagination if there's only 1 page
		&.one-page .pagination-links {
			display: none;
		}

	}

}

/*--------------------------------------------------------------------------------------------
*
*	Pagination buttons & icons
*
*--------------------------------------------------------------------------------------------*/
.acf-admin-field-groups .tablenav-pages .pagination-links .button {
	display: inline-flex;
	align-items: center;
	align-content: center;
	justify-content: center;
	min-width: 40px;
	margin: {
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	};
	padding: {
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	};
	background-color: transparent;

	// Pagination Buttons
	&:nth-child(1),
	&:nth-child(2),
	&:last-child,
	&:nth-last-child(2) {
		display: inline-block;
		position: relative;
		text-indent: 100%;
		white-space: nowrap;
		overflow: hidden;
		margin: {
			left: 4px;
		}

		// Pagination Button Icons
		&:before {
			$icon-size: 20px;
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			$icon-size: $icon-size;
			background-color: $link-color;
			border-radius: 0;
			-webkit-mask-size: $icon-size;
			mask-size: $icon-size;
			-webkit-mask-repeat: no-repeat;
			mask-repeat: no-repeat;
			-webkit-mask-position: center;
			mask-position: center;
		}

	}

	// First Page Icon
	&:nth-child(1):before {
		-webkit-mask-image: url('../../images/icons/icon-chevron-left-double.svg');
		mask-image: url('../../images/icons/icon-chevron-left-double.svg');
	}

	// Previous Page Icon
	&:nth-child(2):before {
		-webkit-mask-image: url('../../images/icons/icon-chevron-left.svg');
		mask-image: url('../../images/icons/icon-chevron-left.svg');
	}

	// Next Page Icon
	&:nth-last-child(2):before {
		-webkit-mask-image: url('../../images/icons/icon-chevron-right.svg');
		mask-image: url('../../images/icons/icon-chevron-right.svg');
	}

	// Last Page Icon
	&:last-child:before {
		-webkit-mask-image: url('../../images/icons/icon-chevron-right-double.svg');
		mask-image: url('../../images/icons/icon-chevron-right-double.svg');
	}

	// Pagination Button Hover State
	&:hover {
		border-color: $blue-600;
		background-color: rgba($link-color, .05);

		&:before {
			background-color: $blue-600;
		}

	}

	// Pagination Button Disabled State
	&.disabled {
		background-color: transparent !important;

		&.disabled:before {
			background-color: $gray-300;
		}

	}

}

/*---------------------------------------------------------------------------------------------
*
*  Empty state
*
*---------------------------------------------------------------------------------------------*/
.acf-no-field-groups-wrapper {
	display: flex;
	justify-content: center;
	padding: {
		top: 48px;
		bottom: 48px;
	};

	.acf-no-field-groups-inner {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-content: center;
		align-items: flex-start;
		text-align: center;
		max-width: 380px;
		min-height: 320px;

		img,
		h2,
		p {
			flex: 1 0 100%;
		}

		h2 {
			@extend .h2;
			margin: {
				top: 32px;
				bottom: 0;
			};
			padding: 0;
			color: $gray-700;
		}

		p {
			@extend .p2;
			margin: {
				top: 12px;
				bottom: 0;
			};
			padding: 0;
			color: $gray-500;

			&.acf-small {
				display: block;
				position: relative;
				margin: {
					top: 32px;
				};
				@extend .p6;
			}

		}


		img {
			max-width: 284px;
			margin: {
				bottom: 0;
			};
		}

		.acf-btn {
			margin: {
				top: 32px;
			};
		}

	}

};

.acf-no-field-groups {

	#the-list tr:hover td,
	#the-list tr:hover th,
	.acf-admin-field-groups .wp-list-table tr:hover,
	.striped > tbody > :nth-child(odd), ul.striped > :nth-child(odd), .alternate {
		background-color: transparent !important;
	}

	.wp-list-table {

		thead,
		tfoot {
			display: none;
		}

	}

	.no-pages {
		display: none;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Small screen list table info toggle
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	.wp-list-table .toggle-row:before {
		top: 4px;
		left: 16px;
		border-radius: 0;
		$icon-size: 20px;
		content: "";
		display: block;
		position: absolute;
		width: 16px;
		height: 16px;
		$icon-size: $icon-size;
		background-color: $link-color;
		border-radius: 0;
		-webkit-mask-size: $icon-size;
		mask-size: $icon-size;
		-webkit-mask-repeat: no-repeat;
		mask-repeat: no-repeat;
		-webkit-mask-position: center;
		mask-position: center;
		-webkit-mask-image: url('../../images/icons/icon-chevron-down.svg');
		mask-image: url('../../images/icons/icon-chevron-down.svg');
		text-indent: 100%;
		white-space: nowrap;
		overflow: hidden;
	}

	.wp-list-table .is-expanded .toggle-row:before {
		-webkit-mask-image: url('../../images/icons/icon-chevron-up.svg');
		mask-image: url('../../images/icons/icon-chevron-up.svg');
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Small screen checkbox
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	@media screen and (max-width: $md) {

		.widefat th input[type="checkbox"],
		.widefat thead td input[type="checkbox"],
		.widefat tfoot td input[type="checkbox"] {
			margin-bottom: 0;
		}

	}

}