/*---------------------------------------------------------------------------------------------
*
*  Hide WP default controls
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	h1.wp-heading-inline {
		display: none;
	}

	.wrap .wp-heading-inline + .page-title-action {
		display: none;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Headerbar
*
*---------------------------------------------------------------------------------------------*/
.acf-headerbar {
	display: flex;
	align-items: center;
	position: sticky;
	top: 104px;
	z-index: 700;
	box-sizing: border-box;
	min-height: 72px;
	margin: {
		left: -20px;
	};
	padding: {
		top: 8px;
		right: 32px;
		bottom: 8px;
		left: 32px;
	};
	background-color: #fff;
	box-shadow: $elevation-01;

	.acf-headerbar-inner {
		flex: 1 1 auto;
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: $max-width;
	}

	.acf-page-title {
		margin: {
			top: 0;
			right: 16px;
			bottom: 0;
			left: 0;
		};
		padding: {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
		};
	}

	@media screen and (max-width: $md) {
		position: static;
	}

	@media screen and (max-width: 600px) {
		justify-content: space-between;
		position: relative;
		top: 46px;
		min-height: 64px;
		padding: {
			right: 12px;
		};
	}

	.acf-headerbar-content {
		flex: 1 1 auto;
		display: flex;
		align-items: center;

		@media screen and (max-width: $md) {
			flex-wrap: wrap;

			.acf-headerbar-title,
			.acf-title-wrap {
				flex: 1 1 100%;
			}

			.acf-title-wrap {
				margin: {
					top: 8px;
				};
			}

		}

	}

	.acf-headerbar-title-field {
		min-width: 320px;

		@media screen and (max-width: $md) {
			min-width: 100%;
		}

	}

	.acf-headerbar-actions {
		display: flex;

		.acf-btn {
			margin: {
				left: 8px;
			};
		};

	}

}

/*---------------------------------------------------------------------------------------------
*
*  Edit Field Group Headerbar
*
*---------------------------------------------------------------------------------------------*/
.acf-headerbar-field-editor {
	position: sticky;
	top: 104px;
	z-index: 700;
	margin: {
		left: -20px;
	};
	width: calc(100% + 20px);

	@media screen and (max-width: $md) {
		position: relative;
		top: 0;
		width: 100%;
		margin: {
			left: 0;
		};
		padding: {
			right: 8px;
			left: 8px;
		};
	}

	@media screen and (max-width: $sm) {
		position: relative;
		top: 46px;
	}


	.acf-headerbar-inner {

		@media screen and (max-width: $md) {
			flex-wrap: wrap;
			justify-content: flex-start;
			align-content: flex-start;
			align-items: flex-start;
			width: 100%;

			.acf-page-title {
				flex: 1 1 auto;
			}

			.acf-headerbar-actions {
				flex: 1 1 100%;
				margin-top: 8px;
				gap: 8px;

				.acf-btn {
					width: 100%;
					display: inline-flex;
					justify-content: center;
					margin: 0;
				}

			}

		}

	}

	.acf-page-title {
		margin: {
			right: 16px;
		};
	}

}

