		}

	}

	.hndle {
		padding: {
			top: 0;
			right: 24px;
			bottom: 0;
			left: 24px;
		};
	}

}


/*---------------------------------------------------------------------------------------------
*
*  Custom ACF postbox header
*
*---------------------------------------------------------------------------------------------*/
.acf-postbox-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	box-sizing: border-box;
	min-height: 64px;
	margin: {
		top: -24px;
		right: -24px;
		bottom: 0;
		left: -24px;
	};
	padding: {
		top: 0;
		right: 24px;
		bottom: 0;
		left: 24px;
	};
	border-bottom: {
		width: 1px;
		style: solid;
		color: $gray-200;
	};

	h2.acf-postbox-title {
		margin: {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
		};
		padding: {
			top: 0;
			right: 24px;
			bottom: 0;
			left: 0;
		};
		@extend .h3;
		color: $gray-700;
	}

	.acf-icon {
		background-color: $gray-400;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Screen options button & screen meta container
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	#screen-meta-links {
		margin: {
			right: 32px;
		};

		.show-settings {
			border-color: $gray-300;
		}

		@media screen and (max-width: 768px) {
			margin: {
				right: 16px;
				bottom: 0;
			};
		}

	}

	#screen-meta {
		border-color: $gray-300;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Postbox headings
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	#poststuff {

		.postbox-header {

			h2,
			h3 {
				justify-content: flex-start;
				margin: {
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
				};
				padding: {
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
				};
				@extend .h3;
				color: $gray-700 !important;
			}

		}

	}

}

/*---------------------------------------------------------------------------------------------
*
* Postbox drag state
*
*---------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	&.is-dragging-metaboxes .metabox-holder .postbox-container .meta-box-sortables {
		box-sizing: border-box;
		padding: 2px;
		outline: none;
		background-image: repeating-linear-gradient(0deg, $gray-500, $gray-500 5px, transparent 5px, transparent 10px, $gray-500 10px), repeating-linear-gradient(90deg, $gray-500, $gray-500 5px, transparent 5px, transparent 10px, $gray-500 10px), repeating-linear-gradient(180deg, $gray-500, $gray-500 5px, transparent 5px, transparent 10px, $gray-500 10px), repeating-linear-gradient(270deg, $gray-500, $gray-500 5px, transparent 5px, transparent 10px, $gray-500 10px);
		background-size: 1.5px 100%, 100% 1.5px, 1.5px 100% , 100% 1.5px;
		background-position: 0 0, 0 0, 100% 0, 0 100%;
		background-repeat: no-repeat;
		border-radius: $radius-lg;
	}

	.ui-sortable-placeholder {
		border: none;
	}

}

/*--------------------------------------------------------------------------------------------
*
*  Search summary
*
*--------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	.subtitle {
		display: inline-flex;
		align-items: center;
		height: 24px;
		margin: 0;
		padding: {
			top: 4px;
			right: 12px;
			bottom: 4px;
			left: 12px;
		};
		background-color: $blue-50;
		border: {
			width: 1px;
			style: solid;
			color: $blue-200;
		};
		border-radius: $radius-md;
		@extend .p3;

		strong {
			margin: {
				left: 5px;
			};
		}

	}

}

/*--------------------------------------------------------------------------------------------
*
*  Action strip
*
*--------------------------------------------------------------------------------------------*/
.acf-actions-strip {
	display: flex;

	.acf-btn {
		margin: {
			right: 8px;
		};
	}

}

/*--------------------------------------------------------------------------------------------
*
*  Notices
*
*--------------------------------------------------------------------------------------------*/
.post-type-acf-field-group {

	.notice,
	#lost-connection-notice {
		position: relative;
		box-sizing: border-box;
		min-height: 48px;
		margin: {
			top: 0 !important;
			right: 0 !important;
			bottom: 16px !important;
			left: 0 !important;
		};
		padding: {
			top: 13px !important;
			right: 16px !important;
			bottom: 12px !important;
			left: 50px !important;
		};
		background-color: #E7EFF9;
		border: {
			width: 1px;
			style: solid;
			color: #9DBAEE;
		};
		border-radius: $radius-lg;
		box-shadow: $elevation-01;
		color: $gray-700;

		&.update-nag {
			display: block;
			position: relative;
			width: calc(100% - 44px);
			margin: {
				top: 48px !important;
				right: 44px !important;
				bottom: -32px !important;
				left: 12px !important;
			};
		}

		.button {
			height: auto;
			margin: {
				left: 8px;
			};
			padding: 0;
			border: none;
			@extend .p5;
		}

		>div {
			margin: {
				top: 0;
				bottom: 0;
			};
		}

		p {
			flex: 1 0 auto;
			margin: 0;
			padding: 0;

			&.help {
				margin: {
					top: 0;
				};
				padding: {
					top: 0;
				};
				@extend .p7;
				color: rgba($gray-700,.7);
			}

		}

		// Dismiss button
		.notice-dismiss {
			position: absolute;
			top: 4px;
			right: 8px;

			&:before {
				content: '';
				$icon-size: 20px;
				display: block;
				position: relative;
				z-index: 600;
				width: $icon-size;
				height: $icon-size;
				background-color: $gray-500;
				border: none;
				border-radius: 0;
				-webkit-mask-size: contain;
				mask-size: contain;
				-webkit-mask-repeat: no-repeat;
				mask-repeat: no-repeat;
				-webkit-mask-position: center;
				mask-position: center;
				-webkit-mask-image: url('../../images/icons/icon-close.svg');
				mask-image: url('../../images/icons/icon-close.svg');
			}

			&:hover::before {
				background-color: $gray-700;
			}

		}

		// Icon base styling
		&:before {
			content: '';
			$icon-size: 16px;
			display: block;
			position: absolute;
			top: 15px;
			left: 18px;
			z-index: 600;
			width: $icon-size;
			height: $icon-size;
			margin: {
				right: 8px;
			};
			background-color: #fff;
			border: none;
			border-radius: 0;
			-webkit-mask-size: contain;
			mask-size: contain;
			-webkit-mask-repeat: no-repeat;
			mask-repeat: no-repeat;
			-webkit-mask-position: center;
			mask-position: center;
			-webkit-mask-image: url('../../images/icons/icon-info-solid.svg');
			mask-image: url('../../images/icons/icon-info-solid.svg');
		}

		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 9px;
			left: 12px;
			z-index: 500;
			width: 28px;
			height: 28px;
			background-color: $color-info;
			border-radius: $radius-md;
			box-shadow: $elevation-01;
		}

		.local-restore {
			align-items: center;
			margin: {
				top: -6px;
				bottom: 0;
			};

		}

	}

	.notice.is-dismissible {
		padding: {
			right: 56px;
		};
	}

	// Success notice
	.notice.notice-success {
		background-color: #EDF7EF;
		border-color: #B6DEB9;

		&:before {
			-webkit-mask-image: url('../../images/icons/icon-check-circle-solid.svg');
			mask-image: url('../../images/icons/icon-check-circle-solid.svg');
		}

		&:after {
			background-color: $color-success;
		}

	}

	// Error notice
	.notice.notice-error,
	#lost-connection-notice {
		background-color: #F7EEEB;
		border-color: #F1B6B3;

		&:before {
			-webkit-mask-image: url('../../images/icons/icon-warning.svg');
			mask-image: url('../../images/icons/icon-warning.svg');
		}

		&:after {
			background-color: $color-danger;
		}

	}

}