*
*  Button icons
*
*---------------------------------------------------------------------------------------------*/
.acf-btn i.acf-icon {
  width: 20px;
  height: 20px;
  -webkit-mask-size: 20px;
  mask-size: 20px;
  margin-right: 6px;
  margin-left: -4px;
}
