/*---------------------------------------------------------------------------------------------
*
*  Updates layout
*
*---------------------------------------------------------------------------------------------*/
.custom-fields_page_acf-settings-updates {

	.acf-settings-wrap {
		display: grid;
		grid-template-columns: 65% 1fr;
		grid-template-rows: min-content 1.3fr;
		gap: 32px 32px;
		grid-template-areas:
		"acf-admin-notice acf-admin-notice"
		". .";

		@media screen and (max-width: $md) {
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-start;
			align-content: flex-start;
			align-items: center;
			grid-column-gap: 8px;
			grid-row-gap: 8px;
		}

	}

	.acf-admin-notice,
	.notice {
		grid-area: acf-admin-notice;
		margin-bottom: 0 !important;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  ACF Box
*
*---------------------------------------------------------------------------------------------*/
.custom-fields_page_acf-settings-updates {

	.acf-box {
		margin: {
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
		};

		.inner {
			padding: {
				top: 24px;
				right: 24px;
				bottom: 24px;
				left: 24px;
			};
		}

		@media screen and (max-width: $md) {
			flex: 1 1 100%;
		}

	}

}

/*---------------------------------------------------------------------------------------------
*
*  Notices
*
*---------------------------------------------------------------------------------------------*/
.custom-fields_page_acf-settings-updates .acf-settings-wrap {

	.acf-admin-notice {
		flex: 1 1 100%;
		margin: {
			top: 16px;
			right: 0;
			bottom: 0;
			left: 0;
		};
	}

}

/*---------------------------------------------------------------------------------------------
*
* License information
*
*---------------------------------------------------------------------------------------------*/
#acf-license-information {

	.acf-activation-form {
		margin: {
			top: 24px;
		};
	}

	label {
		font-weight: bold;
	}

	.acf-input-wrap {
		margin: {
			top: 8px;
			bottom: 24px;
		};
	}

	#acf_pro_license {
		width: 100%;
	}

}

/*---------------------------------------------------------------------------------------------
*
*  Update information table
*
*---------------------------------------------------------------------------------------------*/
#acf-update-information {

	.form-table {

		th,
		td {
			padding: {
				top: 0;
				right: 0;
				bottom: 24px;
				left: 0;
			};
			@extend .p4;
			color: $gray-700;
		}

	}

	.acf-update-changelog {
		margin: {
			top: 8px;
			bottom: 24px;
		};
		padding: {
			top: 8px;
		};
		border-top: {
			width: 1px;
			style: solid;
			color: $gray-200;
		};
		color: $gray-700;

		h4 {
			margin: {
				bottom: 0;
			};
		}

		p {
			margin: {
				top: 0;
				bottom: 16px;
			};

			&:last-of-type {
				margin: {
					bottom: 0;
				};
			}

			em {
				@extend .p6;
				color: $gray-500;
			}

		}

	}

	.acf-btn {
		display: inline-flex;
	}

}